body {
  --primary-color: #036D77;
  --secoondary-color: #E19478;
  --c-color-1: #001B20;
  --c-color-2: #FDDCD1;
  --header-font: "Source Serif Pro";
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
  font-weight: 400;
}
a :hover {
  text-decoration: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  line-height: 1em;
  margin: 0px;
}

p {
  margin: 0px;
}

.header {
  font-family: var(--header-font);
  font-weight: 400;
  color: var(--c-color-2);
  text-decoration: none;
  text-align: center;
  font-size: 22px;
  display: flex;
  flex-direction: column;
}
.header a {
  color: var(--c-color-2);
  text-decoration: none;
}
.header-sub {
  background-color: var(--primary-color);
  min-height: 45px;
  justify-content: center;
  align-items: center;
  display: none;
  margin: 0px;
}
.header-sub h2 {
  font-size: 22px;
  font-style: italic;
  padding: 0px 40px 0px 0px;
  font-weight: 400;
}
.header-sub p {
  font-size: 20px;
}
.header-content {
  min-height: 183.37px;
  background-color: var(--c-color-1);
  margin: 0px;
  padding: 20px 0px 0px;
  display: block;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header-content .menu {
  display: none;
}
.header-content .mobile-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.header-content .mobile-menu .menu-logo img {
  width: 100%;
  max-width: 183.24px;
}
.header-content .mobile-menu .toggle-menu {
  position: absolute;
  cursor: pointer;
  padding: 5px;
  top: 10px;
  right: 20px;
}
.header-content .mobile-menu .toggle-menu i {
  font-size: 29px;
}

.modal-dialog {
  max-width: auto;
  margin: 0px;
  height: 100%;
}
.modal-content {
  height: 100%;
  border: none;
  border-radius: 0px;
  background-color: var(--c-color-1);
  padding: 0px;
}
.modal-header {
  display: flex;
  justify-content: center !important;
  border: none;
}
.modal-header img {
  max-width: 150px;
}
.modal-header button.close {
  position: absolute;
  right: 20px;
  color: var(--c-color-2);
  opacity: 1 !important;
}
.modal-header button.close :hover {
  color: var(--c-color-2);
}
.modal-body .menu-left-menu-container ul {
  padding: 10px 0px 0px;
  margin: 0px;
}
.modal-body .menu-left-menu-container ul a {
  display: block;
  font-size: 24px;
  line-height: 2.5em;
  font-family: var(--header-font);
  font-weight: 400;
  color: var(--c-color-2);
  text-decoration: none;
  text-align: center;
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 100%;
  }
}
@media (min-width: 950px) {
  .header {
    font-family: var(--header-font);
    color: var(--c-color-2);
  }
  .header a {
    color: var(--c-color-2);
  }
  .header-sub {
    background-color: var(--primary-color);
    min-height: 45px;
    justify-content: center;
    align-items: center;
    display: flex;
  }
  .header-content {
    min-height: 140px;
    font-size: 20px;
    padding: 27px 2.5%;
    display: flex;
  }
  .header-content .menu {
    display: grid;
    grid-template-columns: 42.5% 15% 42.5%;
    justify-self: center;
    align-self: start;
  }
  .header-content .menu-logo a img {
    width: 100%;
    height: auto;
    max-width: 200px;
  }
  .header-content .menu ul {
    margin: 0px;
    padding: 0px;
  }
  .header-content .menu ul:not(.sub-menu) {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
  .header-content .menu ul .sub-menu {
    margin: 0px;
  }
  .header-content .menu ul .sub-menu li {
    padding: 0px !important;
  }
  .header-content .menu ul .sub-menu li a {
    font-size: 16px;
    line-height: 2em;
  }
  .header-content .menu ul li {
    padding: 0px 20px;
  }
  .header-content .menu ul li a {
    display: flex;
    text-align: center;
  }
  .header-content .mobile-menu {
    display: none;
  }
  .modal, .modal-backdrop {
    display: none !important;
  }
}
.news {
  background-color: var(--c-color-2);
  min-height: 220px;
}
.news h2 {
  font-size: 35px;
  font-family: var(--header-font);
  font-style: italic;
}
.news p {
  font-family: "Open Sans";
  padding-top: 10px;
}

footer {
  background-color: var(--c-color-1);
}
footer .footer {
  padding: 50px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
footer .footer p, footer .footer a {
  font-size: 22px;
  font-family: var(--header-font);
  font-weight: 400;
  color: var(--c-color-2);
  text-decoration: none;
  text-align: center;
}
footer .footer-col-1-content {
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
}
footer .footer-col-2 .social-icons {
  padding: 20px 0px;
  display: flex;
  justify-content: center;
}
footer .footer-col-2 .social-icons img {
  max-width: 40px;
  margin-right: 16px;
}

@media (min-width: 950px) {
  footer .footer {
    padding: 0px 20px;
    min-height: 360px;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    align-items: center;
    justify-content: center;
  }
  footer .footer p, footer .footer a {
    text-align: left;
  }
  footer .footer-col-1-content {
    display: block;
  }
  footer .footer-col-2 .social-icons {
    padding: 7px 0px 0px;
    display: block;
  }
  footer .footer-col-2 .social-icons img {
    max-width: 40px;
    margin-right: 16px;
  }
}